function setupUltrasonic() {
    Blockly.Arduino.definitions_fnc_.fnc_ultrasonic = "double fnc_ultrasonic_distance(int _t, int _e){\n\tunsigned long dur=0;\n\tdigitalWrite(_t, LOW);\n\tdelayMicroseconds(5);\n\tdigitalWrite(_t, HIGH);\n\tdelayMicroseconds(10);\n\tdigitalWrite(_t, LOW);\n\tdur = pulseIn(_e, HIGH, 18000);\n\tif(dur==0)return 999.0;\n\treturn (dur/57);\n}\n"
}
