function setupEncoder(a, b) {
    var c = "encoder_" + a + "_" + b;
    Blockly.Arduino.definitions_["var_" + c] = "volatile long " + c + "_pos=0;";
    Blockly.Arduino.definitions_["var_" + c + "_A"] = "boolean " + c + "_A_set =false;";
    Blockly.Arduino.definitions_["var_" + c + "_B"] = "boolean " + c + "_B_set =false;";
    Blockly.Arduino.definitions_["var_" + c + "_rotating"] = "static boolean " + c + "_rotating=false;";
    var d = "";
    d = "esp8266" == profile["default"].micro || "esp32" == profile["default"].micro ? d + ("void ICACHE_RAM_ATTR fnc_" + c + "_intA(){\n") : d + ("void fnc_" +
        c + "_intA(){\n");
    d = d + ("\tif ( " + c + "_rotating ) delay (1);\n\tif( digitalRead(") + (a + ") != " + c + "_A_set ) {\n\t\t") + (c + "_A_set = !" + c + "_A_set;\n\t\tif ( ") + (c + "_A_set && !" + c + "_B_set ) " + c + "_pos += 1;\n");
    d += "\t\t" + c + "_rotating = false;\n";
    d += "\t}\n";
    d += "}\n\n";
    var e = "";
    e = "esp8266" == profile["default"].micro || "esp32" == profile["default"].micro ? e + ("void ICACHE_RAM_ATTR fnc_" + c + "_intB(){\n") : e + ("void fnc_" + c + "_intB(){\n");
    e = e + ("\tif ( " + c + "_rotating ) delay (1);\n\tif( digitalRead(") + (b + ") != " + c + "_B_set ) {\n\t\t") +
        (c + "_B_set = !" + c + "_B_set;\n\t\tif ( ") + (c + "_B_set && !" + c + "_A_set ) " + c + "_pos -= 1;\n");
    e += "\t\t" + c + "_rotating = false;\n";
    e += "\t}\n";
    e += "}\n\n";
    Blockly.Arduino.definitions_fnc_["fnc_" + c + "_intA"] = d;
    Blockly.Arduino.definitions_fnc_["fnc_" + c + "_intB"] = e;
    Blockly.Arduino.setups_["setup_input_" + a] = "pinMode(" + a + ", INPUT);";
    Blockly.Arduino.setups_["setup_input_" + b] = "pinMode(" + b + ", INPUT);";
    Blockly.Arduino.setups_["setup_pullup_" + a] = "digitalWrite(" + a + ",HIGH);";
    Blockly.Arduino.setups_["setup_pullup_" + b] = "digitalWrite(" +
        b + ",HIGH);";
    Blockly.Arduino.setups_["setup_" + c + "_int_" + a] = "attachInterrupt(0, fnc_" + c + "_intA, CHANGE);";
    Blockly.Arduino.setups_["setup_" + c + "_int_" + b] = "attachInterrupt(1, fnc_" + c + "_intB, CHANGE);"
}
