    `,
  collapse: true,
  comments: true,
  disable: false,
  maxBlocks: Infinity,
  trashcan: true,
  horizontalLayout: false,
  toolboxPosition: 'start',
  css: true,
  rtl: false,
  scrollbars: true,
  sounds: true,
  oneBasedIndex: true,
  zoom: {
    controls: false,
    wheel: true,
    startScale: 1,
    maxScale: 3,
    minScale: 0.3,
    scaleSpeed: 1.05,
  },
  media: 'media/',
  grid: {
    spacing: 15,
    length: 2,
    colour: '#a2c9d4',
    snap: true,
  },
};
export default {
  name: 'BlocklyView',
  components: {
    BlocklyComponent,
  },
  data() {
    return {
      code: '',
      options,
      filenameToSave: '',
    };
  },
  mounted() {
    // Add a change for render the code automatically
    this.$refs.foo.workspace.addChangeListener(this.renderContent);
    this.loadBlocks();
    window.addEventListener('unload', this.auto_save_and_restore_blocks, false);
    if ('BlocklyStorage' in window) {
      BlocklyStorage.backupOnUnload(this.$refs.foo.workspace);
    }
  },
  computed: {
    ...mapGetters({ buttonExport: 'clickedSaveButton' }),
    ...mapGetters({ buttonImport: 'clickedImportButton' }),
    ...mapGetters({ buttonScreenShoot: 'clickedScreenCaptureButton' }),
    ...mapGetters({ theFile: 'importFile' }),
  },
  updated() {
    this.auto_save_and_restore_blocks();
  },
  watch: {
    buttonExport() {
      if (this.buttonExport === true) {
        // this.exportCode();
      }
      // this.clearSaveState();
    },
    buttonImport() {
      if (this.buttonImport === true) {
        this.importBlocksFile(this.theFile);
      }
      this.clearImportState();
    },
    buttonScreenShoot() {
      if (this.buttonScreenShoot === true) {
        // Blockly.downloadScreenshot(Blockly.getMainWorkspace());
      }

      // this.clearScreenShootState();
    },
  },
  methods: {
    ...mapMutations(['toggleCodeShelf', 'setCodeTab']),
    ...mapMutations(['clearSaveState']),
    ...mapMutations(['clearImportState']),
    ...mapMutations(['clearScreenShootState']),
    renderContent() {
      this.code = Blockly.Arduino.workspaceToCode(this.$refs.foo.workspace);
    },
    loadBlocks() {
      let loadOnce;
      try {
        loadOnce = window.sessionStorage.loadOnceBlocks;
      } catch (e) {
        loadOnce = null;
      }
      if (loadOnce) {
        // Language switching stores the blocks during the reload.
        delete window.sessionStorage.loadOnceBlocks;
        const xml = Blockly.Xml.textToDom(loadOnce);
        Blockly.Xml.domToWorkspace(xml, this.$refs.foo.workspace);
      } else {
        Blockly.Xml.domToWorkspace(document.getElementById('startBlocks'), this.$refs.foo.workspace);
      }
      Blockly.Events.clearPendingUndo();
    },
    persist() {
      localStorage.myBlocks = this.myBlocks;
    },
    auto_save_and_restore_blocks() {
      if (window.sessionStorage) {
        const xml = Blockly.Xml.workspaceToDom(Blockly.getMainWorkspace());
        const text = Blockly.Xml.domToText(xml);
        window.sessionStorage.loadOnceBlocks = text;
      }
    },
    exportCode() {
      this.clearSaveState();

      const xml = Blockly.Xml.workspaceToDom(Blockly.getMainWorkspace());
      const text = Blockly.Xml.domToText(xml);
      const link = document.createElement('a');
      try {
        if (this.filenameToSave === '') {
          this.filenameToSave = `HB${Date.now()}`;
        }

        link.download = `${this.filenameToSave}.xml`;
        link.href = `data:application/octet-stream;utf-8,${encodeURIComponent(text)}`;
        document.body.appendChild(link);
        link.click();
        link.remove();
      } catch (e) {
        window.location.href = `data:application/octet-stream;utf-8,${encodeURIComponent(text)}`;
        // eslint-disable-next-line no-alert
        alert(e);
      }
      // Clear the model
      this.filenameToSave = '';
    },
    exportScreenShoot() {
      // http://gitlab.m5stack.com/o2bk/blockly/commit/e92bae1dda32d53687f6f402d17ddcc683ea4998
      this.clearScreenShootState();
      Blockly.downloadScreenshot(Blockly.getMainWorkspace());
      /*
      this.clearScreenShootState();
      const link = document.createElement('a');
      const dataUri = link.toDataURL('image/png');

      try {
        if (this.filenameToSave === '') {
          this.filenameToSave = `HB${Date.now()}`;
        }

        link.download = `${this.filenameToSave}.png`;
        link.target = '_self';
        link.href = dataUri;
        document.body.appendChild(link);
        link.click();
        link.remove();
      } catch (e) {
        // eslint-disable-next-line no-alert
        alert(e);
      }
      // Clear the model
      this.filenameToSave = '';
      */
    },
    importBlocksFile(element) {
      try {
        const file = element;
        const fr = new FileReader();
        let xml;
        // eslint-disable-next-line func-names
        fr.onloadend = function () {
          xml = Blockly.Xml.textToDom(fr.result);
          Blockly.getMainWorkspace().clear();
          Blockly.Xml.domToWorkspace(xml, Blockly.getMainWorkspace());
        };

        fr.readAsText(file);
      } catch (e) {
        // eslint-disable-next-line no-alert
        alert(e);
      }
    },
  },
};
</script>

